.priceTable {
  margin: 2rem auto;

  width: 400px;
  max-width: 100%;

  padding: 0.5rem 2rem;

  background-color: lightblue;

  .section {
    display: flex;
    justify-content: center;
    flex-direction: column;

    text-align: center; }

  .title {
    marin: 0.5rem 0;

    text-align: center;
    font-size: larger; }

  .button {
    margin: 0.25rem 0.5rem;
    padding: 0.5rem 1.5rem;

    border: solid 1px black;
    border-radius: 0.75rem; }

  .checktextContainer {
    display: flex;

    padding: 0 5%;

    .checktext {
      flex-grow: 1;
      text-align: start; }

    .checkbox {
      appearance: none;
      content: '';

      height: 1rem;
      width: 1rem;

      background-color: transparent;

      box-sizing: content-box;
      border: solid 1px black;
      border-radius: 0.25rem;

      &:checked {
        background-color: darkblue;

        &::after {
          content: 'x';
          display: block;

          height: 100%;
          width: 100%;

          color: lightblue;

          text-align: center; } } } }

  .value {
    margin-left: 0.5rem;

    color: darkblue; }

  .total {
    color: darkgreen;
    font-size: xx-large;
    font-weight: bold; } }
